import React from 'react';
import { useRouter } from 'shared/hooks';
import { useAppState } from 'shared/state';
import EntityContainer from 'modules/entity/containers';
import { ClientGroupEntity } from 'shared/typings/client.types';
import {
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { FaUsers } from 'react-icons/fa';
import {
	DetailsInfo,
	TableBody,
	TableContainer,
	TableHead
} from '../../../../shared/components/Elements';

export function ClientGroupDetails() {
	const { params } = useRouter();
	const { companyId } = useAppState();

	const clientGroupId = parseInt((params as { clientGroupId: string })?.clientGroupId);
	if (!companyId) {
		return null;
	}

	return (
		<div className="py-8">
			<h1 className="mb-6 text-2xl font-semibold dark:text-white">Client Group Details</h1>
			<EntityContainer.One
				id={clientGroupId}
				relations={''}
				entity="client-group"
				name="ClientGroupSingle"
				url={`/${companyId}/categories/${clientGroupId}/`}
				params={{}}
			>
				{({ item, isFetched }) => {
					console.log('item client group', item);
					return (
						<>
							{!isFetched ? (
								<Loader tiled withOverlay />
							) : (
								<div className="">
									<DetailsInfo
										text={(item as ClientGroupEntity).name}
										title="Client Group Name"
										wrapperClassnames="bg-kgrey-bgmenu max-w-xs"
										textClassName="!text-lg font-semibold"
									>
										<FaUsers className="text-kgrey" />
									</DetailsInfo>
									<TableContainer classes={{ root: 'relative ' }}>
										<StyledTable
											aria-label="simple table"
											classes={{ root: 'table-fixed dark-mode-styled-table' }}
										>
											<TableHead>
												<StyledTableRow>
													<StyledTableHeadCell style={{ width: '5%' }}>#</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '95%' }}>
														Client Name
													</StyledTableHeadCell>
												</StyledTableRow>
											</TableHead>
											<TableBody>
												{((item as ClientGroupEntity)?.clients ?? []).map((item, idx) => {
													const { name, id } = item;
													return (
														<StyledTableRow key={id}>
															<StyledTableCell>{idx + 1}</StyledTableCell>
															<StyledTableCell>{name}</StyledTableCell>
														</StyledTableRow>
													);
												})}
											</TableBody>
										</StyledTable>
									</TableContainer>
								</div>
							)}
						</>
					);
				}}
			</EntityContainer.One>
		</div>
	);
}
